import React from "react";

const ContentContainer = ({
  children,
  className,
  fullWidth,
}: React.PropsWithChildren<{ className?: string; fullWidth?: boolean }>) => {
  return (
    <div
      className={`flex w-full ${
        fullWidth ? "" : "max-w-7xl mx-auto"
      } ${className}`}
    >
      {children}
    </div>
  );
};

export default ContentContainer;
