import React, { useState } from "react";
import { Link } from "gatsby";

import { ILayoutMarkupData } from "../../types/commonMarkup.type";
import { ILang } from "../../types/common.type";

import { AiOutlineMenu } from "@react-icons/all-files/ai/AiOutlineMenu";
import { AiOutlineClose } from "@react-icons/all-files/ai/AiOutlineClose";
import { RiContactsBookFill } from "@react-icons/all-files/ri/RiContactsBookFill";

import Button from "../common/button";
import ContentContainer from "../common/contentContainer";
import ContactLinks from "./contactLinks";
import ScrollProgress from "../common/scrollProgress";
import NavLink from "./navLink";

interface INavbarProps extends ILang {
  path: string;
  layoutMarkup: ILayoutMarkupData;
}

const Navbar = ({ path, layoutMarkup, lang }: INavbarProps) => {
  const [isBurgerOpen, setIsBurgerOpen] = useState<boolean>(false);
  const [isContactsOpen, setIsContactsOpen] = useState<boolean>(false);

  const homePageLinkLabel =
    lang === "ru" ? "Домашняя страница КоудКэйв" : "CodeCave home page";

  return (
    <nav className="sticky top-0 z-50 bg-secondary-100 px-6">
      <ContentContainer>
        <div className="w-full">
          <div className="mx-auto max-w-7xl w-full">
            <div className="relative flex h-20 items-center justify-between">
              {/* burger buttons */}
              <div className="relative inset-y-0 left-0 flex items-center lg:hidden">
                <button
                  type="button"
                  className="inline-flex items-center justify-center text-main-70 hover:bg-secondary-30"
                  aria-controls="mobile-menu"
                  aria-expanded="false"
                  aria-label="menu"
                  onClick={() => setIsBurgerOpen(!isBurgerOpen)}
                >
                  <AiOutlineMenu
                    className={`text-4xl ${isBurgerOpen ? "hidden" : ""}`}
                  />
                  <AiOutlineClose
                    className={`text-4xl ${isBurgerOpen ? "" : "hidden"}`}
                  />
                </button>
              </div>
              {/* main links */}
              <div className="flex flex-1 items-center justify-center lg:justify-between">
                <Link to="/" aria-label={homePageLinkLabel}>
                  <img
                    src="/logo.svg"
                    alt="logo"
                    itemProp="image"
                    className="w-64 object-contain"
                  />
                </Link>
                <ul
                  className="hidden lg:flex grow justify-evenly lg:basis-4/6"
                  itemScope
                  itemType="https://schema.org/SiteNavigationElement"
                >
                  {Object.entries(layoutMarkup.links).map(([link, title]) => (
                    <NavLink key={link} path={path} link={link} title={title} />
                  ))}
                </ul>
              </div>
              {/* Contacts menu */}
              <div className="relative">
                <Button
                  id="user-menu-button"
                  variant="dark"
                  size="small"
                  aria-expanded="false"
                  aria-haspopup="true"
                  aria-label="contacts menu"
                  onClick={() => setIsContactsOpen(!isContactsOpen)}
                  className="px-0 sm:px-5 md:px-7 text-xl font-bold whitespace-nowrap rounded-full mx-0"
                >
                  <span className="hidden sm:inline">
                    {layoutMarkup.contactUs}
                  </span>
                  <RiContactsBookFill size="20px" className="sm:hidden" />
                </Button>
                <div
                  className={`absolute flex flex-wrap w-full justify-center items-center align-center top-full left-0 z-50 mx-2 gap-2 bg-secondary-100 p-2 shadow shadow-main-10 rounded ${
                    isContactsOpen ? "" : "hidden"
                  }`}
                  role="menu"
                  aria-orientation="vertical"
                  aria-labelledby="user-menu-button"
                  tabIndex={-1}
                  itemScope
                  itemType="https://schema.org/ContactPoint"
                  itemProp="contactPoint"
                >
                  <ContactLinks />
                </div>
              </div>
            </div>
          </div>

          {/* mobile main links */}
          <div
            className={`lg:hidden ${!isBurgerOpen ? "hidden" : ""}`}
            id="mobile-menu"
          >
            <ul
              className="space-y-1 px-2 pb-3 pt-2"
              itemScope
              itemType="https://schema.org/SiteNavigationElement"
            >
              {Object.entries(layoutMarkup.links).map(([link, title]) => (
                <NavLink key={link} path={path} link={link} title={title} />
              ))}
            </ul>
          </div>
        </div>
      </ContentContainer>
      <ScrollProgress />
    </nav>
  );
};

export default Navbar;
