import React from "react";
import { Link } from "gatsby";

import FooterLogo from "../../assets/common/footer-logo.svg";

import ContentContainer from "../common/contentContainer";
import { ILayoutMarkupData } from "../../types/commonMarkup.type";
import { ILang } from "../../types/common.type";
import Requisites from "./requisites";
import GetPortfolioForm from "./getPortfolioForm";
import PoliciesLinks from "./policiesLinks";

interface IFooterProps extends ILang {
  markup: ILayoutMarkupData;
}

const Footer = ({ markup, lang }: IFooterProps) => {
  const { homePageLinkLabel, links, requisites, policies } = markup;

  return (
    <footer className="bg-main-100 border-t-4 border-secondary-100 border-solid py-10 px-6">
      <ContentContainer className="min-h-[8.5rem] pb-10 border-b border-solid border-[#2D2D2D]">
        <nav className="w-full flex items-start flex-col md:flex-row justify-between space-y-10 md:space-y-0">
          {/* Logo */}
          <Link
            to="/"
            aria-label={homePageLinkLabel}
            className="flex items-center justify-start w-max pt-1 md:order-none order-1"
          >
            <FooterLogo />
          </Link>

          {/* Contacts */}
          <Requisites requisites={requisites} lang={lang} />

          <div className="flex flex-col h-full justify-between space-y-10 md:space-y-0 md:order-none order-2">
            {/* Main links */}
            <ul className="flex justify-between md:justify-center flex-wrap shrink w-full md:w-auto">
              {Object.entries(links).map(([link, title], i) => (
                <li key={`mainLinks-${i}`} className="md:px-2 w-max">
                  <Link
                    to={`/${link.toLowerCase().split(" ").join("-")}`}
                    aria-label={`CodeCave ${title.toLowerCase()}`}
                    className="text-lg md:text-xl text-secondary-30 font-medium md:font-bold hover:underline"
                  >
                    {title.toUpperCase()}
                  </Link>
                </li>
              ))}
            </ul>

            <GetPortfolioForm />
          </div>
        </nav>
      </ContentContainer>
      <PoliciesLinks policies={policies} />
    </footer>
  );
};

export default Footer;
