import { Link } from "gatsby";
import React from "react";

const NavLink = ({
  path,
  link,
  title,
}: {
  path: string;
  link: string;
  title: string;
}) => {
  const linkPath = `/${link.toLowerCase().split(" ").join("-")}`;
  const isCurrent = path.includes(linkPath);

  return (
    <li className="mr-6">
      <Link
        to={linkPath}
        aria-label={`CodeCave ${title.toLowerCase()}`}
        className={`text-xl font-bold text-main-100 hover:text-main-10 ${
          isCurrent && "underline"
        }`}
      >
        {title.toUpperCase()}
      </Link>
    </li>
  );
};

export default NavLink;
