import React from "react";
import { IRequisitesMarkup } from "../../types/commonMarkup.type";
import ContactLinks from "./contactLinks";
import { ILang } from "../../types/common.type";

interface IRequisitesProps extends ILang {
  requisites: IRequisitesMarkup;
}

const Requisites = ({
  requisites: { ein, companyCode, street, city, postalCode, country, phone },
  lang,
}: IRequisitesProps) => {
  const contactsTitle = lang === "ru" ? "КОНТАКТЫ" : "CONTACTS";

  return (
    <div className="text-sm text-secondary-30 flex flex-col md:space-y-5 w-full md:w-max md:order-none order-3 whitespace-pre">
      <p className="text-lg md:text-xl font-medium md:font-bold pb-5 md:pb-0">
        {contactsTitle}
      </p>
      <div className="flex md:block">
        <p>{`${ein} `}</p>
        <p>{companyCode}</p>
      </div>
      <div className="flex md:block">
        <p>{`${street} `}</p>
        <p>{`${city}, ${postalCode} `}</p>
        <p>{country}</p>
      </div>
      <a href={`tel:${phone.split(" ").join()}`} title={`call ${phone}`}>
        {phone}
      </a>
      {/* Contacts menu */}
      <div
        className="flex flex-wrap shrink justify-evenly md:justify-between pt-5 md:pt-0"
        itemScope
        itemType="https://schema.org/ContactPoint"
        itemProp="contactPoint"
      >
        <ContactLinks invert size={20} />
      </div>
    </div>
  );
};

export default Requisites;
