import React from "react";
import { ILang } from "../../types/common.type";

const Gtag = ({ lang }: ILang) => {
  const analyticsId = lang === "ru" ? "G-DH196MLBKH" : "G-HPEX5BHBSJ";
  const createScriptElement = () => {
    const codeScript = document.createElement("script");
    codeScript.innerHTML = `
      window.dataLayer = window.dataLayer || [];
      function gtag(){dataLayer.push(arguments);}
      gtag('js', new Date());

      gtag('config', '${analyticsId}', {
        "debug_mode":true,
        "anonymize_ip":false,
        "send_page_view":false
      });`;
  };

  return (
    <>
      {/* Google tag (gtag.js) */}
      <script
        async
        src={`https://www.googletagmanager.com/gtag/js?id=${analyticsId}`}
      />
      <script
        dangerouslySetInnerHTML={{
          __html: `
          var _hsp = (window._hsp = window._hsp || []);
          _hsp.push(["addPrivacyConsentListener", function (consent) {
            console.log("consent>> ", consent);
            if (consent?.categories?.analytics) {
              console.log("allowed");

              window.dataLayer = window.dataLayer || [];
              console.log("window.dataLayer start>> ", window.dataLayer);
              function gtag(){dataLayer.push(arguments);}
              console.log("gtag f created");
              gtag('js', new Date());
              console.log("window.dataLayer gtag fired once>> ", window.dataLayer);

              gtag('config', '${analyticsId}', {
                "debug_mode":true,
                "anonymize_ip":false,
                "send_page_view":false
              });
              console.log("window.dataLayer end>> ", window.dataLayer);
            }
          }]);`,
        }}
      />
    </>
  );
};

export default Gtag;
