import React from "react";

import { FaTelegramPlane } from "@react-icons/all-files/fa/FaTelegramPlane";
import { FaWhatsapp } from "@react-icons/all-files/fa/FaWhatsapp";
import { FaLinkedinIn } from "@react-icons/all-files/fa/FaLinkedinIn";
import { AiOutlineMail } from "@react-icons/all-files/ai/AiOutlineMail";
import { Link } from "gatsby";

const ContactLinks = ({
  invert,
  size,
}: {
  invert?: boolean;
  size?: number;
}) => {
  const color = invert ? "white" : "black";
  const hoverBg = invert ? "bg-main-70" : "bg-secondary-30";
  const iconSize = size ? `${size}px` : "26px";

  const linkClassName = `flex items-center justify-center p-2 hover:${hoverBg} rounded`;

  return (
    <>
      <Link to="/services#formtarget" title="Email" className={linkClassName}>
        <AiOutlineMail size={iconSize} color={color} />
      </Link>
      <a
        href="https://t.me/salaros_com"
        target="_blank"
        title="Telegram"
        itemProp="contactOption"
        className={linkClassName}
      >
        <FaTelegramPlane size={iconSize} color={color} />
      </a>
      <a
        href="https://wa.me/375292366125"
        target="_blank"
        title="Whatsapp"
        itemProp="contactOption"
        className={linkClassName}
      >
        <FaWhatsapp size={iconSize} color={color} />
      </a>
      <a
        href="https://www.linkedin.com/signup/public-profile-join?vieweeVanityName=salaros"
        target="_blank"
        title="Linkedin"
        itemProp="contactOption"
        className={linkClassName}
      >
        <FaLinkedinIn size={iconSize} color={color} />
      </a>
    </>
  );
};

export default ContactLinks;
