import React from "react";

import { PageProps, graphql, useStaticQuery } from "gatsby";

import { IBasePageContext } from "../../types/common.type";

import Navbar from "./navbar";
import Footer from "./footer";
import { BasicMetadata } from "../common/metadata";
import UpButton from "../common/upButton";
import { ILayoutMarkupData } from "../../types/commonMarkup.type";
import Gtag from "./gtag";

const Layout = ({
  path,
  children,
  pageContext: { lang },
}: PageProps<React.PropsWithChildren, IBasePageContext>) => {
  const layoutData = useStaticQuery(query) as {
    allMarkdownRemark?: {
      nodes?:
        | {
            fields: { language: string };
            frontmatter: { layout: ILayoutMarkupData };
          }[]
        | null;
    };
  };

  const layoutMarkupFallback: ILayoutMarkupData = {
    contactUs: "CONTACT US",
    links: {
      services: "Services",
      workflow: "Workflow",
      projects: "Projects",
      blog: "Blog",
    },
    requisites: {
      ein: "EIN: 35-2654711",
      companyCode: "Company Code: 7296176",
      street: "8 The Green STE B",
      city: "Dover",
      postalCode: "DE 19901-3618",
      country: "United States",
      phone: "+1 302 364 0193",
    },
    homePageLinkLabel: "CodeCave home page",
    policies: {
      privacy: {
        title: "Privacy Policy",
        link: "https://www.iubenda.com/privacy-policy/28209101",
      },
      cookie: {
        title: "Cookie Policy",
        link: "https://www.iubenda.com/privacy-policy/28209101/cookie-policy",
      },
    },
  };
  const layoutMarkup =
    layoutData?.allMarkdownRemark?.nodes?.find(
      (node) => node.fields.language === lang
    )?.frontmatter?.layout ?? layoutMarkupFallback;

  return (
    <>
      <Gtag lang={lang} />
      <main
        className={`min-h-screen bg-secondary-100`}
        itemScope
        itemType="https://schema.org/LocalBusiness"
      >
        <BasicMetadata lang={lang} />
        <Navbar path={path} layoutMarkup={layoutMarkup} lang={lang} />
        {children}
        <Footer lang={lang} markup={layoutMarkup} />
        <UpButton />
      </main>
    </>
  );
};

export default Layout;

export const query = graphql`
  query {
    allMarkdownRemark(
      filter: { fields: { sourceName: { eq: "common" } } }
      sort: { frontmatter: { order: ASC } }
    ) {
      nodes {
        fields {
          language
        }
        frontmatter {
          layout {
            links {
              services
              workflow
              projects
              blog
            }
            contactUs
            requisites {
              ein
              companyCode
              street
              city
              postalCode
              country
              phone
            }
            homePageLinkLabel
            policies {
              privacy {
                title
                link
              }
              cookie {
                title
                link
              }
            }
          }
        }
      }
    }
  }
`;
